import React, { lazy, Suspense} from "react";
import { Route, Redirect } from "react-router-dom";
import { ConnectedRouter } from "react-router-redux";
import { connect } from "react-redux";
//import App from "./containers/App/App";
import asyncComponent from "./helpers/AsyncFunc";
import Auth0 from "./helpers/auth0";
//import Loadable from 'react-loadable';
import Center from 'react-center';
import LinearIndeterminate from 'customApp/containers/MaterialUi/LinearIndeterminate';
import LandingPage from 'customApp/containers/LandingPage';
//import Tarot from 'customApp/containers/Tarot';

//console.log('DEBUG_File', new Error().stack);

const Loading = () => <div id="loading">
  <Center>
    <LinearIndeterminate/>
  </Center>
</div>;

//

const LazyLoader_Tarot = lazy(() => import('customApp/containers/Tarot'));

const Tarot = () => <div>
  <Suspense fallback={Loading}>
    <LazyLoader_Tarot />
  </Suspense>
</div>;

//

const LazyLoader_Aviator = lazy(() => import('customApp/containers/Aviator/game.js'));

const Aviator = () => <div>
  <Suspense fallback={Loading}>
    <LazyLoader_Aviator />
  </Suspense>
</div>;

//

/*const App = Loadable({
  loader: () => import('./containers/App/App'),
  loading: Loading,
});

const Signin = Loadable({
  loader: () => import('./containers/Page/signin'),
  loading: Loading,
});

const LandingPage = Loadable({
  loader: () => import('customApp/containers/LandingPage/l_0002'),
  loading: Loading,
});*/

//

const App = asyncComponent(() => import("./containers/App/App"));


//const LandingPage = asyncComponent(() => import("customApp/containers/LandingPage/Model_0003"));

//

const RestrictedRoute = ({ component: Component, isLoggedIn, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      isLoggedIn ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: "/signin",
            state: { from: props.location }
          }}
        />
      )
    }
  />
);

const routes = [
  {
    path: "/",
    component: LandingPage
  },
  {
    path: "/tarot",
    component: Tarot
  },
  {
    path: "/aviator",
    component: Aviator
  },
  {
    path: "/404",
    component: asyncComponent(() => import("./containers/Page/404"))
  },
  {
    path: "/500",
    component: asyncComponent(() => import("./containers/Page/500"))
  },
  {
    path: "/signin",
    component: asyncComponent(() => import("./containers/Page/signin"))
  },
  {
    path: "/signup",
    component: asyncComponent(() => import("./containers/Page/signup"))
  },
  {
    path: "/forgotpassword",
    component: asyncComponent(() => import("./containers/Page/forgotPassword"))
  },
  {
    path: "/resetpassword",
    component: asyncComponent(() => import("./containers/Page/resetPassword"))
  }
];

const routeComponents = routes.map(({path, component}, key) => <Route exact path={path} component={component} key={key} />);

const PublicRoutes = ({ history, isLoggedIn }) => {
  return (
    <ConnectedRouter history={history}>
      <div>
        
        {routeComponents}

        <Route
          path="/auth0loginCallback"
          render={props => {
            Auth0.handleAuthentication(props);
          }}
        />
        <RestrictedRoute
          path="/dashboard"
          component={App}
          isLoggedIn={isLoggedIn}
        />
      </div>
    </ConnectedRouter>
  );
};

export default connect(state => ({
  isLoggedIn: state.Auth.idToken !== null
}))(PublicRoutes);
